.header {
  max-width: 880px;
  margin: 0 auto;
  padding-top: 45px;
  border-bottom: 1px solid rgba(84,84,84,0.7);
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 913px) {
  .header {
    max-width: 600px;
  }
}

@media  screen and (max-width: 614px) {
  .header {
    max-width: 320px;
    padding-top: 28px;
  }
}
