.popup__close {
  background-image: url(../../../images/Close_Icon.svg);
  padding: 0;
  position: absolute;
  top: -40px;
  right: -40px;
  width: 32px;
  height: 32px;
  border: none;
  background-color: transparent;
  background-size: 32px 32px;
  transition: opacity 0.4s;
  cursor: pointer;
}

.popup__close:hover {
  opacity: 0.6;
}

@media screen and (max-width: 614px) {
  .popup__close {
    width: 20px;
    height: 20px;
    background-size: 20px 20px;
    top: -36px;
    right: 0px;
  }
}
